*,
*:before,
*:after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
 
:root {
	--c1: #283337;
	--c2: #b7dcdf;
	--c3: #617680;
	--c4: #a1c2c5;
	--c5: #324045;
	--c6: #aed1d4;
}

html,
body {
	min-height: 100vh;
	overflow-x: hidden;
}

html {
	box-sizing: border-box;
	// scroll-behavior: smooth;
	// scroll-snap-type: y mandatory;
}

body {
	background-color: #f4f4f4;
	transition: background-color 1s ease;
	font-family: "Barlow", sans-serif !important;
	color: var(--c1);
}
.fixed-text
{
	position: fixed;
    transform: rotate(90deg);
    top: 190px;
    left: -90px;
    color: #617680;
    font-size: 12px;
	display: none;
}
/* colours */
.color-black {
	background-color: #000000;
}
.color-white {
	background-color: #ffffff;
}
.color-green {
	background-color: var(--c1);
}

.color-default {
	background-color: #f4f4f4;
}

img, video{
	display: block;
	max-width: 100%;
	&.full {
		width: 100%;
	}
	&.center {
		margin: 0 auto;
	}
}

@import "age";
@import "nui";
@import "components";
@import "header";
@import "footer";
@import "fireflies";
@import "labels";
@import "kumaon";
@import "interactive";
@import "bootstrap";

/* Top Section */

#animationWindow {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 100;
}
#mainSection {
	position: relative;
	// overflow: hidden;
	.intro-title {
		font-size: 8rem;
		line-height: 0.9;
		text-align: center;
		color: var(--c2);
		position: fixed;
		width: 100%;
		top: 200px;
		z-index: -1;
		overflow: hidden;
		.line__inner {
			display: block;
      font-weight: 800;
		}

		&.hidden {
			opacity: 0;
			transition: opacity 0.2s ease-in-out;
		}
	}

	.bottom {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
	}

	.pin-spacer {
		overflow: hidden !important;
		margin: 0 auto !important;
		top: 84% !important;
		width: 10vw !important;
	}

	.mountain {
		padding-top: 200px;

    &.hide{
      transition: all ease-in 0.1s;
      opacity: 0 !important;
    }
	}
}


