.circle-path-anim {
	width: 178px;
	height: 178px;
	position: absolute;
	top: 378px; // 171px;

	.outline-anim {
		path {
			fill: none;
			stroke: var(--c2);
			stroke-dasharray: 0 100;
			transition: all ease-in 1s;
			stroke-width: 1px;
			stroke-width: 0.5;
			stroke-linecap: round;
		}

		&.active {
			path {
				stroke-dasharray: 100 100;
			}
		}
	}

	.m-outline {
		position: absolute;
		top: 73px;
		left: 0;
		right: 0;
		width: 83px;
		margin: auto;
	}
}
