.modal {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.7);
	justify-content: center;
	align-items: center;
	z-index: 9999;
	color: var(--c2) !important;
	backdrop-filter: blur(101px);
}

.modal-content {
	background-color: var(--c1);
	text-align: center;
	padding: 60px;
	border-radius: 8px;
	box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
	width: 600px;
	line-height: 1.5;
	.header-sec {
		position: relative;
	}
}

button {
