  .kumaon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #ffffff;
    gap: 40px;
    padding: 0 60px;
    * {
      // flex: 1;
    }
    .col-34 {
      width: 60%;
    }
    .col-14 {
      width: 40%;
      text-align: "-webkit-left";
    }

    .content {
      display: flex;
      gap: 30px;
      flex-direction: column;
      h2 {
        font-weight: 400;
        font-size: 40px;
      }
    }
    p {
      font-size: 18px;
    }
  }

  #KumaonBottle {
    position: relative;
    max-width: 480px;
    margin-left: auto;
    video {
      position: absolute;
      top: 17%;
      left: 9px;
      width: 100%;
      z-index: 1;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }

  .page-kumaon {
    .main-nav ul a {
      color: #ffffff;
    }
    .banner-sec {
      background-image: url("../img/sneak/KI.jpg");
      min-height: calc(100vh);
      padding-top: 50px;
      background-attachment: fixed;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      color: #ffffff;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    #soundButton {
      display: none;
    }
    .animation-section {
      position: relative;
      height: 7000px;

      #container3D canvas {
        top: 0px;
        opacity: 0;
        transition: all 1s;
        scale: 0.8;
        position: fixed;
        left: 0;
      }
      #container3D canvas.position-fixed {
        opacity: 1;
      }
      #container3D canvas.go-left {
        position: fixed;
        transition: all 0s;
        opacity: 1;
        left: 20%;
        top: 0;
      }
      #container3D canvas.d-fixed {
        position: absolute;
        transition: all 0s;
        opacity: 1;
        left: 20%;
        top: 100%;
      }
    }
    .sticky {
      display: flex;
      align-items: center;
      overflow: hidden;
      position: sticky;
      top: 0;
      height: 100vh;
    }
    .scroll-image {
      display: flex;
      will-change: transform;
      position: fixed;
      top: 0px;
      bottom: 0;
      transform: translateX(80%);
      overflow: hidden;
      align-items: center;
      transition: all 0.1s !important;
      gap: 1vw;
    }
  }

  // .flipped
  $color_1: #fff;
  $background-color_1: transparent;

  .card-detail {
    width: 25vw;
    flex-grow: 1;

    .front-card  { 
      width: 90%;
    }
  }
 
  .flip-box {
    -ms-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d;
    cursor: url('../img/animate/flip-cursor.svg'), pointer;
    &.flipped {
      .flip-box-front {
        -ms-transform: rotateY(-180deg);
        -webkit-transform: rotateY(-180deg);
        transform: rotateY(-180deg);

        .inner {
          left: 10%;
        }
      }
      .flip-box-back {
        -ms-transform: rotateY(0deg);
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
      }
    }
    .inner {
      position: absolute;
      left: 0;
      padding: 24px;
      outline: 1px solid transparent;
      -webkit-perspective: inherit;
      perspective: inherit;
      z-index: 2;
      transform: translateY(-50%) translateZ(60px) scale(0.94);
      -webkit-transform: translateY(-50%) translateZ(60px) scale(0.94);
      -ms-transform: translateY(-50%) translateZ(60px) scale(0.94);
      top: 50%;
      color: #fff;
    }
    .flip-box-front {
      -ms-transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
      transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
      -webkit-transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      -ms-transform: rotateY(0deg);
      -webkit-transform: rotateY(0deg);
      transform: rotateY(0deg);
      -webkit-transform-style: preserve-3d;
      -ms-transform-style: preserve-3d;
      transform-style: preserve-3d;
      position: relative;

      .inner {
        transform: translateZ(60px) scale(0.94);
        -webkit-transform: translateZ(60px) scale(0.94);
        -ms-transform: translateZ(60px) scale(0.94);
        width: 15%;
        padding: 0;
        top: 2%;
        left: 3%;

        img {
          position: absolute;
          top: 30px;
          width: 200px;
          left: 22px;
          padding: 0;
        }
      }
    }
    .flip-box-back {
      position: absolute;
      top: 0;
      left: 0;
      -ms-transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
      transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
      -webkit-transition: transform 0.7s cubic-bezier(0.4, 0.2, 0.2, 1);
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      width: 100%;
      -ms-transform: rotateY(180deg);
      -webkit-transform: rotateY(180deg);
      transform: rotateY(180deg);
      -webkit-transform-style: preserve-3d;
      -ms-transform-style: preserve-3d;
      transform-style: preserve-3d;
      
      .inner { 
        width: 90%;
        color: #a80928;
        font-size: 18px;
        line-height: 24px;
        padding: 35px;
      }
    }
    .flip-box-header {
      font-size: 34px;
    }
    .flip-box-img {
      margin-top: 25px;
    }
    .flip-box-button {
      background-color: $background-color_1;
      border: 2px solid #fff;
      border-radius: 2px;
      color: $color_1;
      cursor: pointer;
      font-size: 20px;
      font-weight: bold;
      margin-top: 25px;
      padding: 15px 20px;
      text-transform: uppercase;
    }
  }
