  @media (max-width: 768px) {
    .page-kumaon .animation-section #container3D img {
      opacity: 0;
      transition: all 1s;
      left: 25%;
      width: 50vw;
      position: fixed;
      top: 100px;

      &.go-left {
        display: none;
      }
    }
    .page-kumaon .scroll-image {
      width: 1000vw;

      .card-detail {
        max-width: 320px;
        width: 100%;
      }
      .flip-box-back ,.flip-box-front { 
        height: 580px;
      }
    }
    #kumaonMain .reveal-type {
      font-size: 24px;
      width: 100%;
    }
    .dark-circle img {
      min-height: 100% !important;
    }
  }

  .sticky-dots-left{
    position: absolute;
    top: -200px;
    width: 15%;
    left: 0;
  }
  .sticky-dots-right{
    position: absolute;
    bottom: -200px;
    width: 15%;
    right: 0;
    transform: rotate(90deg);
  }